<template>
  <div id="app" :style="theme.app">
    <header>
      <span v-if="caps()">
        {{ cap }}
      </span>
    </header>
    <vs-card-group v-if="news()">
      <Card v-for="card in newsList" :key="card" :c="card" v-show="checkImage(card.image_url)" style="background-color: #1e1e1e"></Card>
    </vs-card-group>
    <Table></Table>
    <vs-button @click="news()">update</vs-button>
    <vs-switch dark v-model="th" style="width:55px">
      <template >
        <Sun v-if="!th" ></Sun>
        <Moon v-else style="background-color:transparent;color:white"></Moon>
      </template>
    </vs-switch>
    <Subscribe :theme="theme.sub"></Subscribe>
    <Footer :theme="theme.footer"></Footer>
  </div>
</template>

<script>
import Vue from 'vue'
import Vuesax from 'vuesax'

import 'vuesax/dist/vuesax.css'
//import Navbar from './components/Navbar'
import Moon from 'vue-material-design-icons/WeatherNight.vue';
import Sun from 'vue-material-design-icons/WhiteBalanceSunny.vue'
import Card from "./components/Card";
import Table from "./components/Table";
import Subscribe from "./components/Subscribe";
import Footer from "./components/Footer";

Vue.use(Vuesax, {
  colors: {
    primary:'#5b3cc4',
    success:'rgb(23, 201, 100)',
    danger:'rgb(242, 19, 93)',
    warning:'rgb(255, 130, 0)',
    dark:'rgb(36, 33, 69)'
  }
})

export default {
  name: 'App',
  components: {
    Card,
    Table,
    Subscribe,
    Footer,
    Moon,
    Sun
  },
  methods: {
    checkImage: function (url) {
      const img = new Image();
      img.src = url;

      if (img.complete) {
        return true
      } else {
        img.onload = () => {
          return true
        };
        img.onerror = () => {
          return false
        };
      }
    },
    news: async function () {
      if(this.newsList !== null){
        return true
      }
      let response = await fetch('http://192.168.0.102:5000/news', {
        method: "GET",
            headers: {
          "Access-Control-Allow-Origin": "*",
              "Content-Type": "text/plain"
        }})//"mode" : "no-cors"});
      if (response.ok) {
        let json = await response.json();
        this.newsList = json
        return true
      } else {
        alert("HTTP-Error: " + response.status);
        return false
      }
    },
    caps: async function () {
      if(this.cap !== null){
        return true
      }
      let response = await fetch('http://192.168.0.102:5000/current_cap/bitcoin', {
        method: "GET",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "text/plain"
        }})//"mode" : "no-cors"});
      if (response.ok) {
        let json = await response.json();
        this.cap = json
        return true
      } else {
        alert("HTTP-Error: " + response.status);
        return false
      }
    }
  },
  data: () => ({
    newsList: null,
    theme: {
      footer: "",
      app: "",
      sub: null
    },
    th: false,
    cap: ''
  }),
  watch: {
    th(val) {
      if (val) {
        this.theme.footer = `background-color: #282444; color: #ddd`
        this.theme.app = `background-color: #141222; color: #aaa`
        this.theme.sub = `background-color: #141222;`
      } else {
        this.theme.footer = ``
        this.theme.app = ``
        this.theme.sub = ``
      }
    },
  },
}
</script>

