<template>
  <vs-card @click="url()">
    <template #title>
      <h3 style="
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;">{{ c.title }}</h3>
    </template>
    <template #img>
      <img :src="c.image_url" alt="" style="aspect-ratio: 16 / 9">
    </template>
    <template #text>
      <p style="
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;">
        {{ c.description }}
      </p>
    </template>
    <template #interactions>
      <vs-button danger icon>
        <i class='bx bx-heart'></i>
      </vs-button>
      <vs-button class="btn-chat" shadow primary>
        <i class='bx bx-chat' ></i>
        <span class="span">
            54
          </span>
      </vs-button>
    </template>
  </vs-card>
</template>

<script>
import Fragment from 'vue-fragment';
import Vue from 'vue'
Vue.use(Fragment.Plugin);
import { Plugin } from 'vue-fragment';
Vue.use(Plugin);

export default {
  name: "Card",
  props: {
    card: String,
    c: Object
  },
  methods: {
    url: function () {
      window.open(this.c.link)
    }
  }
}
</script>

